body {
  width: 100vw;
  height: 100vh;
  background: radial-gradient(at 50% -20%, #908392, #0d060e) fixed;
  overflow: hidden;
}

#handboy {
  transform-origin: 98% 98%;
  transform-box: fill-box;
  animation: swing 1.3s ease-in-out infinite alternate;
}

#girllight {
  transform-origin: 0 97%;
  transform-box: fill-box;
  animation: swing 1.3s ease-in-out infinite alternate;
}

#hairgirl {
  transform-origin: 60% 0;
  transform-box: fill-box;
  animation: swinghair 1.3s ease-in-out infinite alternate;
}

#zero {
  transform-origin: bottom;
  transform-box: fill-box;
}

@keyframes swing {
  0% {
    transform: rotate(10deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

@keyframes swinghair {
  0% {
    transform: rotate(6deg);
  }

  100% {
    transform: rotate(-6deg);
  }
}

.btn-return {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  padding: 1rem;
  font-size: 1rem;
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
}

.back-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 25px;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: absolute;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.btn-design {
  color: #fff;
  background: linear-gradient(0deg, #000 0%, #302a2c 100%);
  border: none;
  overflow: hidden;
}

.btn-design:hover {
  color: #fff;
  text-decoration: none;
}

.btn-design:before {
  content: "";
  width: 30px;
  height: 100%;
  background-color: rgba(255, 255, 255, .5);
  animation: shiny 3s ease-in-out infinite;
  display: inline-block;
  position: absolute;
  top: -180px;
  left: 0;
}

.btn-design:hover {
  opacity: .7;
}

.btn-design:active {
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .3), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .2), inset 4px 4px 6px rgba(0, 0, 0, .2);
}

@keyframes shiny {
  0% {
    opacity: 0;
    -webkit-transform: scale(0)rotate(45deg);
  }

  80% {
    opacity: .5;
    -webkit-transform: scale(0)rotate(45deg);
  }

  85% {
    opacity: 1;
    -webkit-transform: scale(4)rotate(45deg);
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(50)rotate(45deg);
  }
}

